@import "sass/index";

.root {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  position: relative;
  pointer-events: none;
}

.winPrice {
  position: absolute;
  bottom: 0;

  background: #03010e;
  color: white;

  border-radius: 8px;
  padding: 2px 30px;

  pointer-events: none;

  @media (max-width: 600px) {
    font-size: 10px;
    padding: 0 10px;
  }
}

.coinImg {
  position: static;
  margin-right: 3px;
  margin-bottom: -2.5px;

  @media (max-width: 600px) {
    width: 10px;
    height: 10px;
    object-fit: contain;
  }
}

.active {
  background: yellowgreen;
  pointer-events: all;
}
