@import "sass/index";

.pawButton {
  border-radius: 8px;
  padding: 5px 30px;
  flex-grow: 1;

  margin: 2.5px;

  color: white;

  & img,
  & svg {
    -webkit-user-select: none;
    -webkit-user-drag: none;
    user-select: none;
    user-drag: none;

    pointer-events: none;

    width: 40px;
    height: 40px;

    @media (max-width: 600px) {
      width: 30px;
      height: 30px;
    }

    @media (max-width: 400px) {
      width: 20px;
      height: 20px;
    }
  }

  @media (max-width: 600px) {
    padding: 0px;
    height: 35px;
  }

  @media (max-width: 400px) {
    height: 32px;
    margin: 1.5px 1px;
    border-radius: 5px;
  }
}

.active {
  background: #00ffd7 !important;
}

.pointer {
  pointer-events: all;
}

.bad {
  color: #aa333a;
}

.load {
  opacity: 0.7;
}

.selected {
  background: rgba(153, 205, 50, 0.567) !important;
}
