@import "sass/index";

.root {
  display: flex;
  align-items: center;
  justify-content: center;

  @media (max-width: 450px) {
    flex-direction: column;
  }
}

.betButton {
  background: linear-gradient(
    275.81deg,
    #2e2eed 15.6%,
    #4e4ef9 75.42%
  ) !important;
  color: white;
  border-radius: 100px;

  font-family: "Gilroy";
  font-weight: 700;
  font-size: 14px;
  text-align: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  line-height: 1;

  // width: 100%;
  min-width: 200px;
  height: 52px;
  padding: 7px 20px;

  margin-right: 20px;

  & > :last-child {
    align-items: flex-end;
    // flex-direction: column;
  }

  @media (max-width: 450px) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.disable {
  opacity: 0.7;
  pointer-events: none;
}

.bottomButtonText {
  & > img {
    margin: 0 3px;
    margin-bottom: -2px;
  }
}
