@import "sass/index";

$padding: 7px;

.historyWrapper {
  display: flex;
  justify-content: flex-end;
  background: rgba(255, 255, 255, 0.15);
  min-height: 42px + $padding * 2;
  padding: $padding;
  border-radius: 10px 10px 0 0;
  overflow-x: hidden;

  & > :last-child {
    transition: max-width 1s, opacity 1s;
    max-width: 1000px;
  }
}

.lastAnimated {
  & > :last-child {
    padding: 0;
    max-width: 0px;
    overflow: hidden;
    opacity: 0;
  }
}

.element {
  background: #00ffd7;
  border-radius: 45px;
  padding: 14px 15px;
  margin: 0 2px;
  height: fit-content;
  cursor: pointer;

  & span {
    color: black;

    font-family: "Gilroy";
    font-weight: 500;
    font-size: 14px;

    text-align: center;
    text-transform: uppercase;
    display: block;
    line-height: 1;
  }
}

.loss {
  background: rgba(0, 0, 0, 0.4);

  & span {
    color: rgba(255, 255, 255, 0.5);
  }
}
