@import "sass/index";

$gap: 10px;

.root {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  margin-top: 20px;

  & > * {
    width: 100%;

    &:first-child {
      margin-right: $gap / 2;
    }
    &:last-child {
      margin-left: $gap / 2;
    }
  }

  @media (max-width: 720px) {
    flex-wrap: wrap;

    & > * {
      &:first-child {
        margin: 0;
      }
      &:last-child {
        margin: 0;

        margin-top: $gap;
      }
    }
  }
}

.disable {
  opacity: 0.7;
  pointer-events: none;
}
