@import "sass/index";

.root {
  border: 2px solid use(--secondary-dark);
  background: rgba(20, 25, 48, 0.3);

  padding: 30px 10px;
}

.option {
  margin-right: 5px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

$color: use(--template-statisticsBlock-tab-color);

.text {
  font-family: "Gilroy";
  font-size: 16px;
  color: $color;
}
.itemTitle {
  font-weight: 600;
}
.selectedItem {
  font-weight: 700;
  text-transform: uppercase;
}
.arrow {
  & svg {
    color: $color;
  }
}

.item {
  width: 18px;
  height: 18px;
  object-fit: contain;
  margin: 0 5px;
}

.selectItem {
  background: use(--info-main);
  color: use(--info-contrastText);

  &:hover {
    background: use(--primary-light);
  }
}

.diffImage {
  margin-right: 5px;
}
