@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
}

.gameBlock {
  background: use(--template-page-joinBox-background);
  overflow: hidden;
  border-radius: 0;
  width: 100%;
  height: 100%;
  display: flex;
}

.statisticsWrapper {
  padding-top: 26px;
}

.gameWrapper {
  width: 100%;
}

.gameCell {
  height: 100%;
  width: 100%;
}

.gameFooter {
  padding: 10px;
  font-weight: 500;

  @media (max-width: 600px) {
    position: relative;
    z-index: 1;
    padding: 5px;
    background: use(--template-page-joinBox-background);
  }

  & input {
    font-weight: 500;
  }
}
