@import "sass/index";

.root {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  max-height: 500px;
  overflow: hidden;
  position: relative;

  & > div {
    position: absolute;
  }

  @media (max-width: 600px) {
    align-items: flex-end;
    height: 450px;
  }

  @media (max-width: 400px) {
    height: 390px;
  }

  @media (max-width: 360px) {
    height: 380px;
  }
}

.background {
  width: 100%;
  height: auto;
  min-width: 1200px;

  object-fit: cover;
  user-select: none;
  user-drag: none;
  -webkit-user-drag: none;

  @media (max-width: $mobileScreen) {
    min-width: 0;
    width: 100%;
    position: absolute;
    top: 0;
  }
}
