@import "sass/index";

$leftPadding: 20px;
$buttonSize: 40px;
$buttonMargin: 10px;
$mobileSize: 500px;

.betInput {
  font-family: "Varela Round";
  font-weight: 400;
  position: relative;

  min-width: 460px;

  @media (max-width: 720px) {
    min-width: 0;
  }

  @media (max-width: $mobileSize) {
    padding-bottom: $buttonSize + $buttonMargin;
  }
}

.titleWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

  color: use(--template-statisticsBlock-tab-color);
  font-size: 14px;
  opacity: 0.5;

  margin-bottom: 10px;
}

.input {
  background: rgba(20, 25, 48, 0.3);
  margin: 0;
  padding: 30px 10px 10px $leftPadding;
  height: auto;

  border-radius: 8px;
  border: 2px solid use(--secondary-dark);
}

.inputRoot {
  margin: 0;
}

.inputElement {
  height: 20px;
  color: use(--template-statisticsBlock-tab-color);

  font-size: 18px;
}

.label {
  font-size: 13px;
  line-height: 16px;

  color: use(--template-statisticsBlock-tab-color);

  mix-blend-mode: normal;
  opacity: 0.5;

  top: 8px;
  left: $leftPadding;
}

.adornment {
  width: $buttonSize * 5;
  height: 0;
  position: relative;

  @media (max-width: $mobileSize) {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    margin: 0;
  }
}

.buttons {
  position: absolute;
  right: 0;
  bottom: -$buttonMargin;

  @media (max-width: $mobileSize) {
    bottom: auto;
    top: $buttonMargin;
    width: 100%;
  }
}

.button {
  padding: 0;
  width: $buttonSize;
  height: $buttonSize;

  background: rgba(255, 255, 255, 0.2) !important;
  border-radius: 0;
  margin: 0 1px;
  color: use(--template-chat-massage-author-color);

  @media (max-width: $mobileSize) {
    width: calc(100% / 4);
    margin: 0;
  }

  &:first-child {
    border-radius: 8px 0 0 8px;
  }

  &:last-child {
    border-radius: 0 8px 8px 0;
  }
}
